import React, { useState } from 'react';
import Storage from './LocalStorage.js'

export default function MonitorSign({historyElement, obj}) {

    let step = Storage.getLocal('distance')
    const [x, setX] = useState(historyElement.x);
    const [y, setY] = useState(historyElement.y);
    const [size, setSize] = useState(historyElement.size);
    const [content, setContent] = useState(obj.monitor);

    return (
        <div>
            <div><b>Content: </b> <br/><input type="text" value={content} onChange={(e)=>{setContent(e.target.value)}}></input></div>
            <div className="numbers">
                <div><b>x: </b><input type="number" min="0" max="2000" step={step} value={x} onChange={(e)=>{setX(e.target.value)}} /></div>
                <div><b>y: </b><input type="number" min="0" max="2000" step={step} value={y} onChange={(e)=>{setY(e.target.value)}} /></div>
                <div><b>s: </b><input type="number" min="0" max="200" value={size} onChange={(e)=>{setSize(e.target.value)}} /></div>
            </div>
        </div>
    )
}
