import React from 'react'
import ThemeContext from './../context/ThemeContext';
import Mouse from './../objects/MouseHandle.js'

export default function KeyBoardBtn({content}) {
    let click = (e, obj) =>{
        let arr = obj.val.map(n => n);
        let element = {
            content:e.target.previousSibling.innerText,
            x:Mouse.getCursor().x,
            y:Mouse.getCursor().y,
            id:Math.round(new Date().getTime()/100)+'',
            size:10
        }
        arr.push(element)
        obj.setVal(arr)
    }
    return (
        <ThemeContext.Consumer>
        {(obj)=>(
            <div>
                <div className="btn-name">{content}</div>
                <div className="btn-content" onMouseDown={(e)=>{
                    click(e, obj)
                }}>{content}</div>
            </div>
        )}
        </ThemeContext.Consumer>
    )
}
