import React from 'react'
import Storage from './LocalStorage.js'
import GridLine from './GridLine.js'

let defaultDistance = 10
let defaultBoardSize = 2000

export default function Grid() {

  let distance = Storage.getLocal('distance', defaultDistance)
  let boardSize = Storage.getLocal('boardSize', defaultBoardSize)
  let gridColor = Storage.getLocal('gridColor', '#008000')
  
  let lines = new Array(boardSize/distance); 
  

  for (let index = 0; index < lines.length; index++) {
    lines[index] = distance*index
  }

  const mystyle = {
    left: '0px',
    top: '0px',
    position: 'absolute',
    zIndex: '-1'
};
  return (
    <div style={mystyle}>
      {
        lines.map(
        vline => <GridLine 
          key={vline} 
          gap={vline} 
          gridColor={gridColor}
          boardSize={boardSize}
          orientation="vertical"></GridLine>)
      }
      {
        lines.map(
        hline => <GridLine 
          key={hline} 
          gap={hline} 
          gridColor={gridColor}
          boardSize={boardSize}
          orientation="horizontal"></GridLine>)
      }
    </div>
  )
}
