import React from 'react'

export default function GridLine({gridColor, gap, orientation, boardSize}) {

    const mystyle = {
        width: '1px',
        position: 'absolute'
    };

    if(orientation === 'vertical'){
        mystyle['height'] = boardSize+'px'
        mystyle['borderLeft'] = '1px solid '+gridColor
        mystyle['left'] = gap+'px'
        mystyle['top'] = '0px'
    } else {
        mystyle['width'] = boardSize+'px'
        mystyle['borderTop'] = '1px solid '+gridColor
        mystyle['left'] = '0px'
        mystyle['top'] = gap+'px'
    }
    
    return (
        <div style={mystyle}></div>
    )
}