import React from 'react'
import Mouse from './../objects/MouseHandle.js'
import ThemeContext from './../context/ThemeContext';


export default function ElementCreator({element}) {
    let self = null
    let monitor = null
    let follower = (e)=>{
        self.style.left = Mouse.getCursor().x+'px'
        self.style.top = Mouse.getCursor().y+'px'
        
        if(self !== null && monitor !== null){
            monitor.setMonitor('aaaaaaaaaa')
            // console.log(self.toString(), monitor)    
        }
        
    }
    
    let down = (e) => {
        Mouse.setMoveSubscriber(follower)
        Mouse.setUpSubscriber(()=>{
            
            Mouse.setMoveSubscriber(null)
        })
    }
    Mouse.setMoveSubscriber(follower)
    Mouse.setUpSubscriber(()=>{
        Mouse.setMoveSubscriber(null)
    })
    const mystyle = {
        position: 'absolute',
        userSelect: 'none',
        left: element.x+'px',
        top: element.y+'px',
        zIndex: 1
    };
    return (
        <ThemeContext.Consumer>
        {(obj)=>(
           
            <span style={mystyle} onMouseDown={down}  ref={elem =>{self = elem; monitor = obj} }>
                {element.content} 
            </span>
        )}
        </ThemeContext.Consumer>
    )
}
