import React from 'react'
import './../css/Monitor.css'
import MonitorSign from './MonitorSign.js'
import MonitorGraph from './MonitorGraph.js'
import ThemeContext from './../context/ThemeContext';

export default function Monitor() {
    let historyElement = {x:10, y:10, content:"content", size: 15, id:'asdas'}
    let historyElement2 = {strokeStyle:'rgba(255, 255, 255, 1)', content:"[10,10,10,10,10]", lineWidth: 15, id:'asdasda'}
    let monitorElement = null
    let type = "sign"
    if(type === "sign") {
        monitorElement = <MonitorSign historyElement={historyElement}></MonitorSign>
    } else {
        monitorElement = <MonitorGraph historyElement={historyElement2}></MonitorGraph>
    }
    return (
        <ThemeContext.Consumer>
        {(obj)=>(
            <div className="Monitor">
                {/* {monitorElement} */}
                <MonitorSign historyElement={historyElement} obj={obj}></MonitorSign>
                <hr></hr>
                <MonitorGraph historyElement={historyElement2} obj={obj}></MonitorGraph>
            </div>
        )}
        </ThemeContext.Consumer>
            
    )
}
