import React from 'react'
import KeyBoardBtn from './KeyBoardBtn.js'
import './../css/KeyBoard.css'

export default function KeyBoard() {

    let buttons = '1234567890abcdefghijklmnopqrstuvwxyz'
    let btn = Array.from(buttons)
    return (
        <div className="KeyBoard">
            {btn.map((letter)=><KeyBoardBtn key={'btn-'+letter} content={letter}></KeyBoardBtn>)}
            <div>
                <div className="btn-name">&nbsp;</div>
                <div className="btn-content">+</div>
            </div>
        </div>
    )
}
